.laptop {
  height: fit-content;
}

.laptop .header {
  height: 197px;
  display: flex;
  align-items: center;
  position: relative;
}

.laptop .header .previousPage {
  position: absolute;
  width: 53px;
  height: 53px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  left: 6.4%;
  margin-bottom: 25px;
}

.laptop .header .title {
  width: fit-content;
  margin: 0 auto;
}

/* START OF INFO CONTAINER */

.infoContainer {
  width: 1178px;
  margin: 0 auto;
}

.infoDivider {
  display: flex;
  column-gap: 68px;
  height: 100%;
}
.infoContainer .firstRow {
  height: 342px;
  margin-bottom: 84px;
}

.infoContainer .firstRow .laptopImg {
  min-width: 577px;
  height: 342px;
}

.infoContainer .firstRow .userInfo {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  row-gap: 33px;
}

.secondRow {
  margin-top: 54px;
  margin-bottom: 77px;
}

.secondRow .infoDivider {
  display: flex;
}

.secondRow .laptopInfo {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  row-gap: 33px;
}

.secondRow .CPUInfo {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  row-gap: 33px;
}

.thirdRow {
  margin-top: 54px;
  margin-bottom: 77px;
}

.thirdRow .infoDivider {
  display: flex;
}

.infoDivider.bottom {
  column-gap: 107px;
}

.thirdRow .laptopState {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  row-gap: 33px;
}

.thirdRow .buyDate {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  row-gap: 33px;
}

/* END OF INFO CONTAINER */

/* START OF INFO LABEL */
.infoContainer .infoLabel {
  display: flex;
  width: 100%;
}

.infoLabel .key {
  font-size: 22px;
  font-weight: 500;
  color: #2e2e2e;
  width: 281px;
  margin-right: 19px;
}

.infoLabel .value {
  font-size: 20px;
  font-weight: 400;
  color: #797979;
}

.cont {
  width: 577px;
}

.value {
  width: 250px;
}

@media screen and (max-width: 1000px) {
  .laptop .header {
    height: 84px;
    width: 390px;
    margin: 0 auto;
  }
  .laptop .header .title {
    font-size: 16px;
    font-weight: 700;
  }
  .laptop .header .previousPage {
    background-color: transparent;
    margin-bottom: 0;
    left: 0;
  }

  .infoDivider {
    flex-direction: column;
  }
  .infoContainer {
    width: 390px;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
  }

  .infoContainer .firstRow {
    width: 358px;
    height: fit-content;
    margin-bottom: 23px;
  }
  .infoContainer .firstRow .laptopImg {
    min-width: 358px;
    width: 358px;
    height: 191px;
    object-fit: cover;
    margin-bottom: 29px;
  }
  .infoContainer .secondRow {
    width: 358px;
    height: fit-content;
  }
  .infoContainer .thirdRow {
    width: 358px;
    height: fit-content;
  }
  hr {
    border: 0.6px solid #a5a5a5;
    width: 358px;
  }
  .infoContainer .firstRow .userInfo {
    row-gap: 0px;
  }
  .infoLabel .key {
    font-size: 14px;
    font-weight: 500;
    line-height: 24px;
    width: 185px;
  }
  .infoLabel .value {
    font-size: 14px;
    font-weight: 500;
    width: auto;
  }
  .secondRow {
    margin-top: 23px;
    margin-bottom: 23px;
  }
  .secondRow .laptopInfo {
    row-gap: 0px;
  }
  .secondRow .CPUInfo {
    row-gap: 0px;
  }
  .thirdRow {
    margin-top: 23px;
    margin-bottom: 23px;
  }
  .thirdRow .laptopState {
    row-gap: 0px;
  }
  .thirdRow .buyDate {
    row-gap: 0px;
  }
}

/* END OF INFO LABEL */
